.DisputeForm-form {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.DisputeForm-form-element {
	width: 80%;
	margin-top: 10px;
	margin-left: auto;
	margin-right: auto;
}

.DisputeForm-form-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.DisputeForm-dispute-button-container {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.DisputeForm-dispute-button {
	-webkit-transition-duration: 0.2s; /* Safari */
    -o-transition-duration: 0.2s;
	   transition-duration: 0.2s;
	background-color: #c60000;
	color: white;
	width: 30%;
	margin-top: 20px;
}

.DisputeForm-dispute-button:hover {
	background-color: white;
	color: #c60000;
}

.DisputeForm-send-button {
	-webkit-transition-duration: 0.2s; /* Safari */
    -o-transition-duration: 0.2s;
	   transition-duration: 0.2s;
    background-color: #00a31b;
	color: white;
}

.DisputeForm-send-button:hover {
	background-color: green;
	color: white;
}


