.InReviewGameCard {
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 90%;
    margin-bottom: 15px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 5%;
    background-color: #f9f9f9;
}

.InReviewGameCard-game-name {
	text-align: center;
	padding-top: 10px;
    font-size: 17pt;
}

.InReviewGameCard-game-state {
	text-align: center;
}

.InReviewGameCard-link-and-proposition {
    text-align: center;
}

.InReviewGameCard-gameLink {
    margin-right: 10px;
}

.InReviewGameCard-total-pool-amount {
	text-align: center;
    font-size: 13pt;
}

.InReviewGameCard-winning-outcome {
    text-align: center;
}

.InReviewGameCard-betting-table {
	border-collapse:collapse; 
	border-spacing: 6px;
	text-align: center;
}

.InReviewGameCard-separator {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    height: 1px;
    margin-left: auto; 
    margin-right: auto;
    background-color: grey;
}

.InReviewGameCard-betting-table-header {
    font-size: 15px;
    text-decoration: underline;
	width: 5%;
	text-align: center;
}

.InReviewGameCard-betting-table-row {
    font-size: 20px;    
}

.InReviewGameCard-winning-outcome-table-row {
    background-color: #bfffc2;
}

.InReviewGameCard-losing-outcome-table-row {
    background-color: #ffd6d6;
}

tr {
    margin-bottom: 10px;
}



