.ResolvedGameCard-resolved-game {
    -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2);
            box-shadow: 0 1px 2px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    background-color: #f7f7f7;
    width: 90%;
    padding: 2%;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
}

.ResolvedGameCard-resolved-game-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.ResolvedGameCard-resolved-game-name {
	text-align: left;
    font-size: 17pt;
}

.ResolvedGameCard-link-and-proposition {
    text-align: center;
}

.ResolvedGameCard-gameLink {
    margin-right: 10px;
}

.ResolvedGameCard-expected-winnings {
    font-size: 12pt;
}

.ResolvedGameCard-resolved-game-subrow {
    font-size: 11pt;
    margin-top: 5px;
}

.ResolvedGameCard-resolved-user-bet-amount {
    margin-top: 5px;
}

.ResolvedGameCard-claim-button {
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    height: 100px;
    width: 25%;
}

.ResolvedGameCard-claim-button:hover {
    background-color: #349929; /* Green */
    color: #efefef;
}




